<template>
  <div class="goods">
      <options :data="dataOption" v-on:change="optchange" ref="goodsOpt"/>
      <tables :data="dataTable" :list="tableList" v-on:change="tabchange"/>
      <div class="demonstration-box">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageParams.size"
          layout="total, prev, pager, next"
          :total="pageParams.totalCount"
        ></el-pagination>
      </div>
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%">
        <span>{{dialogContent}}</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="sure">确 定</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
import Options from 'views/common/options'
import Tables from 'views/common/tables'
import queryString from "querystring"
import page from 'data/page'

const dataOption = {
  leftMenu: [
    { id:'1', name:'问卷名称：', placeholder:'请输入问卷名称', module:'name'},
    { id:'2', name:'校区名称：', placeholder:'请输入校区名称', module:'name'},
    { id:'3', name:'班级名称：', placeholder:'请输入班级名称', module:'name'},
    { id:'4', name:'带班老师：', placeholder:'请输入带班老师', module:'contactPerson'},
    { id:'5', name:'开始日期：', placeholder:'请选择开始日期', module:'beginTime', type:'date'},
    { id:'6', name:'结束日期：', placeholder:'请选择结束日期', module:'endTime', type:'date'},
  ],
  rightMenu: [
    { id:'r1', name:'查询'}
  ]
}

const dataTable = {
  table: [
    { label:'问卷名称', prop:'name'},
    { label:'校区名称', prop:'school'},
    { label:'班级名称', prop:'class'},
    { label:'带班老师', prop:'contactPerson'},
    { label:'时间范围', prop:'time'},
  ],
  edit: [
    {name: '问卷统计', id: 't3'},
    {name: '删除', id: 't2'},
  ]
}

const tips = ['确认删除该条信息?'];

export default {
  name: 'question',
  mixins: [page],
  data() {
    return {
      dataOption: dataOption,
      dataTable: dataTable,
      dialogVisible: false,
      dialogContent: tips[0],
      tableList: [],
      loading: false,
      currentPage: 1,
      id: ''
    }
  },
  components: {
    Options,
    Tables
  },
  mounted(){
    this.search();
  },
  methods:{
    optchange(params){
      console.log(params.id + '--id被单击了')
      if(params.id === 'r1') return this.search();
    },
    tabchange(params){
      console.log(params.id + '--id被单击了')

      this.id = params.data.id;
      // 问卷统计
      if(params.id === 't3') return this.$router.push(`/service/quesStatistic?type=detail&id=${params.id}`);
      // 删除
      if(params.id === 't2') return this.dialogVisible = true;
    },
    sure(){
      this.search();
      this.dialogVisible = false;
    },
    search(){  //搜索
      const optParams = this.$refs.goodsOpt.getData();
      const pageParams = { size: 20, page: this.currentPage -1 };

      const params = Object.assign({}, optParams, pageParams)
      this.tableList = [
        {name: '张三1', school: '海淀区', class: '三年级二班', contactPerson: '王某某', time: '2018-01-10--2015-02-10'},
        {name: '张三1', school: '海淀区', class: '三年级二班', contactPerson: '王某某', time: '2018-01-10--2015-02-10'},
        {name: '张三1', school: '海淀区', class: '三年级二班', contactPerson: '王某某', time: '2018-01-10--2015-02-10'},
      ]
      // this.loading = true;
      // const param = queryString.stringify(params || {});
      // let url = urls[code];
      // this.api.get(`/role`).then((res)=>{
        // this.loading = false;
        // this.data = res.data.list;
        // this.setPage(res.data)
        // this.dataInit();
      // }).catch((err) => {
      //   console.log(err)
      //   this.$message(err.message)
      // })
    },
    dataInit(){//数据初始化

    }
  }
}
</script>
 
